.header { 
    &_row {
        .row-flex();
        justify-content: space-between;
        align-items: center;
        height: 114px;
        padding-bottom: 9px;
        .lg-block({
            height: inherit;
            min-height: 114px;
            padding: 10px 0 30px;
        });
        .xs-block({
            min-height: inherit;
        });
    }
    &_logo {
        .col();
        .size(2);
        display: flex;
        align-items: center;
        .lg-block({
            order: 1;
        });
        .sm-block({
            position: relative;
            z-index: 11;
        });
        img {
            .sm-block({
                width: 58px;
                height: 58px;
            });
        }
        &--p {
            margin-left: 10px;
            font-size: 18px;
            line-height: 21px;
            letter-spacing: 0.02em;
            text-transform: uppercase;
            color: #36332D;
            .o700();
            .sm-block({
                font-size: 12px;
                line-height: 14px;
            });
        }
    }
    &_nav {
        .col();
        .size(6);
        .size-xlg(8);
        .size-lg(12);
        .lg-block({
            order: 3;
            margin-top: 15px;
        });
        .sm-block({
            padding: 123px 0 50px 20px;
            order: 4;
            position: absolute;
            top: 0; left: 0;
            z-index: -1;
            background-color: #fff;
            margin: 0;
            opacity: 0;
            transition: .4s all ease;
        });
        &.active {
            opacity: 1;
            z-index: 10;
        }
        ul {
            display: flex;
            align-items: center;
            .xlg-block({
                justify-content: center;
            });
            .sm-block({
                flex-direction: column;
                align-items: flex-start;
            });
        }
        li {
            &:not(:first-child) {
                margin-left: 40px;
                .sm-block({
                    margin-left: 0;
                    margin-top: 30px;
                });
            }
            & > a {
                color: @blue;
                font-size: 16px;
                line-height: 22px;
                .o600();
                position: relative;
                transition: .4s all ease;
                .sm-block({
                   font-size: 18px;
                    line-height: 26px;
                });
                &:hover {
                    .o700();
                }
                &::after {
                    content: '';
                    position: absolute;
                    bottom: -4px; left: 0;
                    width: 46px;
                    height: 2px;
                    background: #C22328;
                    opacity: 0;
                    transition: .4s all ease;
                }
                &.active {
                    .o700();
                    .sm-block({
                        .o600();
                    });
                    &::after {
                        opacity: 1;
                        .sm-block({
                            opacity: 0;
                        });
                    }
                }
            }
        }
    }
    &_tel {
        .col();
        .size(2);
        .size-lg(3);
        .size-md(4);
        display: flex;
        flex-direction: column;
        align-self: flex-end;
        align-items: flex-end;
        padding-bottom: 5px;
        .lg-block({
            order: 2;
        });
        .sm-block({
            flex-direction: row;
            padding-bottom: 0;
            align-self: center;
            align-items: center;
            justify-content: flex-end;
            position: relative;
            z-index: 11;
        });
        &--a {
            display: flex;
            p {
                margin-left: 6px;
                font-size: 22px;
                line-height: 145%;
                letter-spacing: 0.02em;
                color: #083844;
                .o700();
                .sm-block({
                    display: none;
                });
            }
        }
        &--btn {
            border-bottom: 1px solid #333333;
            font-size: 16px;
            line-height: 22px;
            color: #333333;
            .o600();
            margin-top: 4px;
            .sm-block({
                display: none;
            });
        }
    }
}

.intro {
    position: relative;
    background-position: bottom -75px right -119px;
    background-size: contain;
    background-repeat: no-repeat;
    min-height: 766px;
    .xlg-block({
        background-position: bottom -75px right -342px;
    });
    .lg-block({
        background-position: bottom -75px right -424px;
    });
    .md-block({
        min-height: 666px;
        background-position: bottom -75px right -566px;
        background-size: cover;
    });
    .xs-block({
        min-height: 473px;
        background-position: bottom -101px right -430px;
    });
    &::before {
        content: '';
        position: absolute;
        top: 0; left: 0;
        width: 55%;
        height: 89.5%;
        background: #F8F8F8;
        z-index: -1;
        .md-block({
            width: 72%;
            height: 93%;
        });
    }
    &_block {
        position: relative;
        padding-top: 115px;  
        .xs-block({
            padding-top: 75px;
        });
        &::before {
            content: 'skiff';
            position: absolute;
            left: 37%;
            bottom: -8%;
            font-size: 200px;
            line-height: 220px;
            letter-spacing: 0.02em;
            text-transform: uppercase;
            color: rgba(8, 56, 68, 0.04);
            .h1();
            .md-block({
                display: none;
            });
        }
    }
    &_h1 {
        font-size: 60px;
        line-height: 120%;
        letter-spacing: 0.02em;
        color: #083844;
        max-width: 1180px;
        .h1();
        .md-block({
            font-size: 40px;
            line-height: 56px;
            max-width: 725px;
        });
        .sm-block({
            font-size: 26px;
            line-height: 32px;
            max-width: 550px;
        });
        .xs-block({
            font-size: 18px;
            line-height: 26px;
            letter-spacing: 0.04em;
        });
    }
    &_desc {
        position: relative;
        font-size: 22px;
        line-height: 30px;
        color: #083844;
        max-width: 690px;
        margin-top: 60px;
        .md-block({
            max-width: 725px;
        });
        .sm-block({
            font-size: 18px;
            line-height: 24px;
            max-width: 500px;
        });
        .xs-block({
            font-size: 14px;
            line-height: 20px;
            margin-top: 50px;
        });
        &::after {
            content: '';
            position: absolute;
            top: -10px; left: 0;
            width: 173px;
            height: 3px;
            background-color: @red;
            .md-block({
                top: -30px;
            });
            .xs-block({
                top: -25px;
            });
        }
    }
    &_btn {
        margin-top: 70px;
        .btn();
        .xs-block({
           margin-top: 25px;
        });
    }
    &_down {
        position: absolute;
        bottom: 0;
        left: 46.3%;
        transform: translateX(-50%);
        width: 80px;
        height: 82px;
        background: #083844;
        display: flex;
        align-items: center;
        justify-content: center;
        cursor: pointer;
        img {
            .md-block({
                width: 30px;
                height: 30px;
            });
        }
        .md-block({
            width: 113px;
            height: 50px;
        });
        .xs-block({
            display: none;
        });
    }
}

