        margin-top: 10px;
        span {
            font-size: 16px;
            .o300();
            color: #fff;
            margin-left: 8px;
            .xs-block({
                margin-left: 0;
            });
        }
        input {
            width: 320px;
            height: 62px;
            background: #FFFFFF;
            padding: 0 20px;
            font-size: 22px;
            line-height: 30px;
            letter-spacing: 0.02em;
            color: #083844;
            margin: 3px 10px 0;
            .xs-block({
                margin: 3px 0 0;
            });
        }
    }
    &_btn {
        .btn();
        margin: 22px auto 0;
        width: 320px;
        .xs-block({
            width: 100%;
            max-width: 320px;
        });
    }
    &_check {
        margin-top: 24px;
        display: flex;
        align-items: center;
        cursor: pointer;
        max-width: 484px;
        .xs-block({
            max-width: inherit;
            align-items: flex-start;
        });
        &--item {
            position: relative;
            top: -12px;
            left: 30px;
            .xs-block({
                position: static;
            });
            input {
                display: none;
            }
            i {
                width: 18px;
                height: 18px;
                border: 1px solid #FFFFFF;
                position: relative;
                display: inline-block;
                &::after {
                    content: '';
                    position: absolute;
                    top: 5px; left: 3px;
                    background: url(../img/check.svg);
                    width: 11px;
                    height: 7px;
                    opacity: 0;
                    transition: .4s all ease;
                }
            }
            input:checked+i::after {
                opacity: 1;
            }
        }
        p {
            margin-left: 6px;
            font-size: 16px;
            line-height: 24px;
            text-align: center;
            text-decoration: underline;
            color: #FFFFFF;
            .o300();
            .xs-block({
                font-size: 14px;
                line-height: 20px;
                text-align: left;
            });
        }
    }
}

@media screen and (max-width:392px) {
    .project_h2 {
        &.h2::after {
            left: 0;
            top: 80px;
        }
    }
}

@media screen and (max-width:367px) {
    .banner_text--btn {
        padding: 16px 30px;
    }
}

@media screen and (max-width:325px) {
    .banner_block::before {
        display: none;
    }
    .form_label input {
        width: 300px;
    }
    .callback_label input {
        width: 300px;
    }
}