.reset();
.container {
    .container();
}
html {
    font-size: 16px;
    line-height: 1.3;
}
a {
    text-decoration: none;
    color: inherit;
}
a:hover {
    text-decoration: none;
}
body {
    font-family: 'OpenSansRegular', 'Arial', sans-serif;
}
ul,li {
    margin: 0;
    padding: 0;
}
li {
    list-style: none;
}
:active, :hover, :focus {
    outline: 0;
    outline-offset: 0;
}
input {
    border: none;
}
button {
    background: none;
    border: none;
    cursor: pointer;
    padding: 0;
}
* {
	box-sizing: border-box;
}

.btn_mnu {
    width: 50px;
    height: 50px;
    display: none;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    cursor: pointer;
    position: relative;
    .sm-block({
        display: flex;
        order: 3;
        margin-left: 10px;
    });
}
.btn_row {
    height: 2px;
    width: 60%;
    margin: 4px 0;
    background-color: #000;
    position: relative;
    transition: all .5s;
}
.btn_mnu.active .btn_row:first-child {
    transform: rotate(-135deg);
    top: 10px;
}
.btn_mnu.active .btn_row:last-child {
    transform: rotate(-225deg);
    top: -10px;
}
.btn_mnu.active .btn_row:nth-child(2) {
    opacity: 0;
}
 
@blue: #083844;
@red: #C22328;

.h2 {
    font-size: 60px;
    line-height: 72px;
    letter-spacing: 0.02em;
    .bold();
    position: relative;
    display: inline-block;
    color: #083844;
    &::after {
        content: '';
        position: absolute;
        top: 55%; left: 107%;
        width: 173px;
        height: 3px;
        background-color: @red;
        .md-block({
            left: 104%;
            width: 50px;
        });
        .sm-block({
            left: 0;
            top: 45px;
        });
    }
    .xlg-block({
        font-size: 54px;
        line-height: 66px;
    }); 
    .lg-block({
        font-size: 48px;
        line-height: 60px;
    }); 
    .md-block({
        font-size: 46px;
        line-height: 55px;
    });
    .sm-block({
        font-size: 26px;
        line-height: 34px;
    });
}

.section {
    margin-top: 110px;
    .sm-block({
        margin-top: 90px;
    });
}

.btn() {
    background-color: @red;
    padding: 16px 40px;
    display: inline-block;
    font-size: 22px;
    color: #fff;
    .o600();
    transition: .4s all ease;
    &:hover {
        background: linear-gradient(180deg, #BE0208 0%, #A00D11 100%);
    }
}