}

.header_nav li > a::after {
  content: '';
  position: absolute;
  bottom: -4px;
  left: 0;
  width: 46px;
  height: 2px;
  background: #C22328;
  opacity: 0;
  transition: .4s all ease;
}

.header_nav li > a.active {
  font-family: 'OpenSansBold', 'Arial', sans-serif;
}

.header_nav li > a.active::after {
