.callback_row {
  margin-top: 8px;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.callback_label {
  display: flex;
  flex-direction: column;
  margin-top: 10px;
}

.callback_label span {
  font-size: 16px;
  font-family: 'Open Sans Light', 'Arial', sans-serif;
  color: #fff;
  margin-left: 8px;
}

.callback_label input {
  width: 320px;
  height: 62px;
  background: #FFFFFF;
  padding: 0 20px;
  font-size: 22px;
  line-height: 30px;
  letter-spacing: 0.02em;
  color: #083844;
  margin: 3px 10px 0;
}

.callback_btn {
  background-color: #C22328;
  padding: 16px 40px;
  display: inline-block;
  font-size: 22px;
  color: #fff;
  font-family: 'OpenSansSemiBold', 'Arial', sans-serif;
  transition: .4s all ease;
  margin: 22px auto 0;
  width: 320px;
}

.callback_btn:hover {
  background: linear-gradient(180deg, #BE0208 0%, #A00D11 100%);
}

.callback_check {
  margin-top: 24px;
  display: flex;
  align-items: center;
  cursor: pointer;
  max-width: 484px;
}

.callback_check--item {
  position: relative;
  top: -12px;
  left: 30px;
}

.callback_check--item input {
  display: none;
}

.callback_check--item i {
  width: 18px;
  height: 18px;
  border: 1px solid #FFFFFF;
  position: relative;
  display: inline-block;
}

.callback_check--item i::after {
  content: '';
  position: absolute;
  top: 5px;
  left: 3px;
  background: url(../img/check.svg);
  width: 11px;
  height: 7px;
  opacity: 0;
  transition: .4s all ease;
}

.callback_check--item input:checked + i::after {
  opacity: 1;
}

.callback_check p {
  margin-left: 6px;
  font-size: 16px;
  line-height: 24px;
  text-align: center;
  text-decoration: underline;
  color: #FFFFFF;
  font-family: 'Open Sans Light', 'Arial', sans-serif;
}

